*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	padding: 0 5px
  } 
  
  
  
  .container{
	  width: 500px;
  
	  font-family: Trebuchet MS;
  }
  
  
  
  .title {
	  color: #4C4C4C;
	  margin-top:20px;
	  font-size: 1.1em;
	  text-align: center;
	  font-weight: 600;
  
	  margin-bottom: 20px;
  
  }
  
  .block {
  
  
  
  }
  .block-left {
  
  
		  &__departure {
			  margin-bottom: 10px;
		  }
  
		  &__month {
			  margin-top: 40px;
		  }
  }
  .departure {
  
		  &__title {
			  margin-bottom: 5px;
			  font-size: 0.6em;
			  color: #BFC3C4;
			  margin-left: 10px;
		  }
  
		  &__input {
			  border: #BFC3C4 1px solid;
			  height:30px;
  
			  width: 100%;
			  border-radius: 3px;
  
			  color: #4C4C4C;
			  font-weight: 500;
			  font-size: 0.8em;
			  padding: 0 10px;
		  }
  }
  .month {
  
		  &__title {
			  margin-bottom: 5px;
			  font-size: 0.6em;
			  color: #BFC3C4;
			  margin-left: 10px;
		  }
  
		  &__blocks {
			  display: grid;
			  grid-template-columns: 1fr 1fr 1fr;
	  
  
  
			  
		  }
  }
  .blocks-month {
	  
  
  
		  &__block {
			  display: flex;
			  align-items: center;
			  justify-content: center;
			  
			  height:40px;
			  font-size: 0.7em;
  
			  text-align: center;
  
			  border: 1px solid #4C4C4C;
  
			  &:nth-child(3){
				  border-bottom: 0;
			  }
			  &:first-child,
			  &:nth-child(2),
			  &:nth-child(4),
			  &:nth-child(5),
			  &:nth-child(7),
			  &:nth-child(8){
				  border-bottom: 0;
				  border-right: 0;
			  }
  
			  &:nth-child(3),
			  &:nth-child(6),
			  &:nth-child(9){
				  border-bottom: 0;
			  }
  
			  &:nth-child(10),
			  &:nth-child(11){
				  border-right: 0;
			  }
  
			  
  
		  }
  
		  &__title {
			  margin: 5px 0 3px 0;
			  font-size: 1em;
			  font-weight: 600;
			  color: #4C4C4C;
  
  
		  }
  
		  &__price {
			  margin-bottom: 5px;
			  color: #BFC3C4;
			  font-size: 0.7em;
		  }
  }
  
  .block-left {
  
	  &__departure {
		  margin-bottom: 20px;
	  }
  }
  .maxprice {
  
	  &__title {
		  margin-top: 20px;
	  }
  }
  .departure {
	  position: relative;
  
	  &__title {
	  }
  }
  
  .countries {
  position: absolute;
	  z-index: 1000;
	  min-height: 1px;
	  max-height: 200px;
	  width: 70%;
  
	  overflow-x: auto;
  
	  background: white;
	  box-shadow: 2px 5px 10px 0px rgba(80, 80, 80, 0.612); 
  

	  cursor: pointer;
  
	  &__code{
		  position: absolute;
  
		  font-size: 9px;
  
  
		  top: 50%;
		  right: 5%;
		  transform: translateY(-50%);
  
		  color:#BFC3C4;
  
	  }
  
	  &__item {
		  position: relative;
		  font-size: 0.8em;
		  list-style-type: none;
  
		  border-bottom:#4C4C4C 1px solid;
  
		  padding: 15px 0 15px 10px;
  
  
  
  
		  &:hover{
			  background: #eaeaea;
		  }
  
  
	  }
  }
  
  
  
  
  span{
	  display: block;
  }
  
  input{
	  display: block;
  }
  
  .dnone{
	  display: none;
  }
  
  .activeMonth{
	  background: #F3F8ED;
	  border: #BDCC92 1px solid;
	  border-bottom: 0;
  
  }
  
  .airport{
	  margin-left: 20px;
	  border:none;
  
	  &__city{
		  display: inline-block;
		  color:#BFC3C4
	  }
  }